import React from "react";
import img from "../images/G3.png";
import "../css/LandingPage.css";
import { LandingData } from "../data/WebsiteData";
const LandingPage = () => {
  return (
    <div className="body">
      <div className="container">
        <div className="header">
          <h1>{LandingData[0].heading}</h1>
        </div>
        <img src={img} alt="land" className="landing-image toggle " />
        <div className="para">
          <h5>
            {LandingData[0].para1}
            <br />
            {LandingData[0].para2}
            <br />
            {LandingData[0].para3}
            <br />
          </h5>
        </div>
        <div className="d-grid gap-3 d-md-flex justify-content-md-start buttons ">
          <button className="btn btn-primary me-md-1" type="button">
            Get Quote
          </button>
          <button className="btn btn-primary" type="button">
            Learn More
          </button>
        </div>
      </div>
   
    </div>
  );
};

export default LandingPage;
