import React from "react";
import Card from "./Card";

const CardList = ({ facility }) => {
  return (
    <div className="container dib pa4 ma2  ">
      {facility.map((user, i) => {
        //pure components
        return (
          <Card
            key={i}
            facility={facility[i].facility}
            name={facility[i].name}
            id={facility[i].id}
          />
        );
      })}
    </div>
  );
};
export default CardList;
