import React from "react";
import image from '../images/create.png'
const Card = ({ name, facility, id }) => {
  return (
    <div className=' bg-light-grey dib pa4 ma3 grow  shadow-3'>
        <img alt="robots" src={image} style={{width:"150px" ,height:"150px"}} />
      <h5>{name}</h5>
      <h5>{facility}</h5>
    </div>
  );
};
export default Card;
