import React, {  Component } from "react";
import "../src/css/App.css";
import { Switch, Route } from "react-router-dom";
import Navbar from "./components/Navbar";
import LandingPage from "./pages/Landing";
import Contact from "./pages/Contact";
import Footer from "./components/Footer";
import Tutoring from "./pages/Tutoring";
import Signin from "./pages/Signin";
import Register from "./pages/Register";
import SummerCourses from "./pages/SummerCourses";
import Tutors from "./pages/Tutors";
import Testimonials from "./pages/Testimonials";
import Fade from "react-reveal/Fade";

const initialState = {
  input: "",
  imageUrl: "",
  box: {},
  route: "/",
  isSignedIn: false,
  componentHide: false,
  user: {
    id: "",
    name: "",
    email: "",
    entries: 0,
    joined: "",
  },
};
class App extends Component {
  constructor() {
    super();
    this.state = initialState;
  }

  loadUser = (data) => {
    this.setState({
      user: {
        id: data.id,
        name: data.name,
        email: data.email,
        entries: data.entries,
        joined: data.joined,
      },
    });
  };
  onRouteChange = (route) => {
    if (route === "signout") {
      this.setState(initialState);
    } else if (route === "/") {
      this.setState({ isSignedIn: true });
    }
    this.setState({ route: route });
  };
  render() {
    const { isSignedIn, route } = this.state;
    return (
      <div className="App">
        <Navbar isSignedIn={isSignedIn} onRouteChange={this.onRouteChange} />

        <Switch>
          <Route id="landing" path="/" component={LandingPage} />
          <Route
            id="summercourses"
            path="/summercourses"
            component={SummerCourses}
          />
          <Route path="tutoring" component={Tutoring} />
          <Route path="tutors" component={Tutors} />
          <Route path="login" component={Signin} />
          <Route path="contactUs" component={Contact} />
        </Switch>

        <div className="tutoring" data-aos="fade-up">
          <Fade bottom>
            <Tutoring />
          </Fade>
        </div>

        <div className="tutors">
          <Fade bottom>
            {" "}
            <Tutors />
          </Fade>
        </div>

        <div className="testimonials">
          <Fade bottom>
            <Testimonials />{" "}
          </Fade>
        </div>

        {isSignedIn === false ? (
          <div className="login">
            {route === "signin" ? (
              <Signin
                loadUser={this.loadUser}
                onRouteChange={this.onRouteChange}
              />
            ) : (
              <Register
                loadUser={this.loadUser}
                onRouteChange={this.onRouteChange}
              />
            )}
          </div>
        ) : null}
        <div className="contactUs">
          <Contact />
        </div>
        <Footer className="footer" />
      </div>
    );
  }
}

export default App;
